import { ENUM_AUDIO_CLIP, ENUM_GAME_MODE, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from './../StaticInstance';
import AudioManager from "../manager/AudioManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import { TelegramWebApp } from "../telegram/TelegramMiniApp";
import { UserDailyTasksDto } from "../dtos/user.dto";
import PoolManager from "../manager/PoolManager";
import DailyTaskItem from "../ui/DailyTaskItem";
import { UserController } from "../api/controllers/AuthController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class TasksLayer extends BaseLayer {

    dailyPanel: cc.Node = null
    invitePanel: cc.Node = null

    btnDailyTab: cc.Node = null
    btnInviteTab: cc.Node = null
    btnInviteFriend: cc.Node = null

    dailyPanelContent: cc.Node = null

    //bottomPanel
    btnMain: cc.Node = null
    btnLeaderboard: cc.Node = null
    btnTasks: cc.Node = null
    btnProfile: cc.Node = null

    onLoad() {
        this.btnMain = cc.find('tabs_panel/btn_main', this.node)
        this.btnLeaderboard = cc.find('tabs_panel/btn_leader', this.node)
        this.btnTasks = cc.find('tabs_panel/btn_tasks', this.node)
        this.btnProfile = cc.find('tabs_panel/btn_profile', this.node)

        this.btnMain.on('click', this.onMainClick, this)
        this.btnLeaderboard.on('click', this.onLeaderboardClick, this)
        this.btnTasks.on('click', this.onTasksClick, this)
        this.btnProfile.on('click', this.onProfileClick, this)

        this.dailyPanel = cc.find('daily', this.node)
        this.invitePanel = cc.find('invite', this.node)

        this.dailyPanelContent = cc.find('main/scroll_view/view/content', this.dailyPanel)

        this.btnDailyTab = cc.find('btn_tab_daily', this.invitePanel)
        this.btnInviteTab = cc.find('btn_tab_invite', this.dailyPanel)
        this.btnInviteFriend = cc.find('main/task_item_invite/panel/btn_invite', this.invitePanel)

        this.btnDailyTab.on('click', this.onTabDailyClick, this)
        this.btnInviteTab.on('click', this.onTabInviteClick, this)
        this.btnInviteFriend.on('click', this.onInviteFriendClick, this)
    }

    onDestroy() {
        this.btnMain.off('click', this.onMainClick, this)
        this.btnLeaderboard.off('click', this.onLeaderboardClick, this)
        this.btnTasks.off('click', this.onTasksClick, this)
        this.btnProfile.off('click', this.onProfileClick, this)

        this.btnDailyTab.off('click', this.onTabDailyClick, this)
        this.btnInviteTab.off('click', this.onTabInviteClick, this)
        this.btnInviteFriend.off('click', this.onInviteFriendClick, this)
    }

    async onEnable(){
        while (DataManager.instance.userData == null) {
            await new Promise(resolve => setTimeout(resolve, 10)); // Wait 10ms before checking again
        }

        if(DataManager.instance.userTasks){
            this.setDailyTasks(DataManager.instance.userTasks);
        }

        this.fetchDailyTasks();
    }

    fetchDailyTasks(){
        UserController.Instance?.getUserDailyTasks().then((response) => {
            DataManager.instance.userTasks = response.data;
            this.setDailyTasks(response.data);
        }).catch((error) => {
            console.error("Failed to fetch tasks:", error);
        });
    }

    setDailyTasks(dto: UserDailyTasksDto){
        this.dailyPanelContent.destroyAllChildren();
        const poolManager = PoolManager.instance;

        for (let i = 0; i < dto.tasks.length; i++) {
            const item = poolManager.getNode(`task_item_new`, this.dailyPanelContent).getComponent(DailyTaskItem);
            item.init(dto.tasks[i]);
        }
    }

    onTabDailyClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.dailyPanel.active = true;
        this.invitePanel.active = false;
    }

    onTabInviteClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.dailyPanel.active = false;
        this.invitePanel.active = true;
    }
    
    onInviteFriendClick() { 
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        TelegramWebApp.Instance.shareReferral();
    }

    onMainClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU)
    }

    onLeaderboardClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD)
    }

    onTasksClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS)
    }

    onProfileClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE)
    }

}
