import { uuid } from "../Utils";
import AudioManager from "./AudioManager";
import { ICallBack, IUser } from "./DataManager";

export default class SdkManager {
    public static _instance: SdkManager = null

    public static get instance() {
        if (null == this._instance) {
            this._instance = new SdkManager();
        }
        return this._instance
    }

    shareTitle: string = 'This game is fun, do you dare to accept the challenge?'
    shareImg: string = ''
    // 
    videoId: string = ''
    private videoAd = null
    // 
    interstitialId: string = ''
    private interstitialAd = null
    // 
    bannerId: string = ''
    private bannerAd = null
    // : 90%
    customRowId: string = ''
    private customRowAd = null
    // : 80%
    customColId: string = ''
    private customColAd = null
    // 
    videoRecorder: any = null; // 
    videoRecordState: number = 0; // 
    videoStartTime: number = null; // 
    videoRecordTime: number = 120; // 
    videoPath: string = null; // 
    videoIsExist: number = 0; // , 
    videoTopics: string[] = []

    // 
    getPlatform() {
        let platform = null
        if (cc.sys.platform == cc.sys.WECHAT_GAME) {
            platform = window['wx']
        } else if (cc.sys.platform == cc.sys.BYTEDANCE_GAME) {
            platform = window['tt']
        }
        return platform
    }

    // 
    // query: string (eg: key1=val1&key2=val2)
    activeShare(options: { title?: string, imageUrl?: string, query?: string } = { title: '', imageUrl: '', query: '' }) {
        const platform = this.getPlatform()
        if (!platform) {
           //console.log('[Active sharing] Only supports mini game platforms!')
            return
        }
        options.title = options.title || this.shareTitle
        options.imageUrl = options.imageUrl || this.shareImg
        platform.shareAppMessage(options);
    }

    // 
    passiveShare(options: { title?: string, imageUrl?: string, query?: string } = { title: '', imageUrl: '', query: '' }) {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log('[Passive sharing] Only supports mini game platforms!')
            return
        }
        platform.showShareMenu({
            success: (res: any) => { },
            fail: (res: any) => { }
        });
        options.title = options.title || this.shareTitle
        options.imageUrl = options.imageUrl || this.shareImg
        platform.onShareAppMessage(() => {
            return options
        });
    }

    // 
    getShareQuery(key: string) {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log('[Get sharing parameters] Only supports mini-game platforms!')
            return
        }
        const options = platform.getLaunchOptionsSync()
        const query = options.query
        let data = null
        if (query && query[key]) {
            data = query[key]
        }
        return data
    }

    // 
    turnToApp(appId: string) {
        if (appId == '') return
        const platform = this.getPlatform()
        if (!platform) {
            this.turnToBrowser(appId)
            return
        }
        platform.navigateToMiniProgram({
            appId: appId
        });
    }

    // 
    turnToBrowser(url: string) {
        window.open(url)
    }

    // 
    initAudioEndListener() {
        if (typeof window['wx'] === 'undefined') {
            //console.log('[Audio Interruption Monitoring] Only supports WeChat platform!')
            return
        }
        window['wx'].onAudioInterruptionEnd(() => {
            //console.log('Audio interrupted, resume playback')
            AudioManager.instance.playMusic()
        })
    }

    // 
    initBannerAd() {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log('[Traffic main banner initialization] Only supports small game platforms!')
            return
        }
        if (this.bannerId == '') {
            //console.log('[Traffic Master] Please configure banner advertising ID')
            return
        }
        let winSize = platform.getSystemInfoSync();
        if (this.bannerAd == null) {
            this.bannerAd = platform.createBannerAd({
                adUnitId: this.bannerId,
                adIntervals: 30,
                style: {
                    height: winSize.windowHeight - 80,
                    left: 0,
                    top: 500,
                    width: winSize.windowWidth
                }
            });
            this.bannerAd.onResize((res: any) => {
                this.bannerAd.style.top = winSize.windowHeight - this.bannerAd.style.realHeight;
                this.bannerAd.style.left = winSize.windowWidth / 2 - this.bannerAd.style.realWidth / 2;
            });
            this.bannerAd.onError((err: any) => {
                //console.error('[Traffic main banner] Initialization error')
            });
        }
    }

    // 
    toggleBannerAd(isShow: boolean) {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log(`[Traffic main banner:${isShow}] only supports small game platforms!`)
            return
        }
        if (this.bannerAd) {
            isShow ? this.bannerAd.show() : this.bannerAd.hide();
        }
    }

    // 
    initInterstitialAd() {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log('[Traffic main screen initialization] Only supports small game platforms!')
            return
        }
        if (this.interstitialId == '') {
            //console.log('[Traffic Master] Please configure the interstitial advertising ID')
            return
        }
        if (this.interstitialAd == null) {
            this.interstitialAd = platform.createInterstitialAd({
                adUnitId: this.interstitialId
            });
            this.interstitialAd.onError((err: any) => {
                //console.error('[Traffic main screen] Initialization error')
            });
        }
    }

    // 
    showInterstitialAd() {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log('!')
            return
        }
        if (this.interstitialAd) {
            this.interstitialAd.show().catch((err: any) => {
                //console.error('')
            });
        }
    }

    // 
    initVideoAd() {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log('!')
            return
        }
        if (this.videoId == '') {
            //console.log('ID')
            return
        }
        if (this.videoAd == null) {
            this.videoAd = platform.createRewardedVideoAd({
                adUnitId: this.videoId
            });
            this.videoAd.onError((err: any) => {
                //console.error('')
            });
        }
    }

    // 
    showVideoAd(success: any, fail?: any) {
        const platform = this.getPlatform()
        if (!platform) {
            // console.log('')
            return success && success('')
        }
        if (this.videoAd) {
            this.videoAd.offClose();
            this.videoAd.onClose((res: any) => {
                this.videoAd.offClose();
                if (res && res.isEnded || res === undefined) {
                    return success && success('')
                } else {
                    return fail && fail('')
                }
            });
            this.videoAd.show().catch(() => {
                this.videoAd.load()
                    .then(() => this.videoAd.show())
                    .catch((err: any) => {
                        //console.log('')
                    })
            });
        } else {
            // console.log('')
            return fail && fail('')
        }
    }

    // 
    initCustomRowAd() {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log('!')
            return
        }
        if (this.customRowId == '') {
            //console.log('ID')
            return
        }
        let winSize = platform.getSystemInfoSync();
        if (this.customRowAd == null) {
            this.customRowAd = platform.createCustomAd({
                adUnitId: this.customRowId,
                adIntervals: 30,
                style: {
                    width: 320,
                    left: (winSize.screenWidth - 320) / 2,
                    top: winSize.screenHeight - 100,
                    fixed: 0
                }
            })
            this.customRowAd.onError((err: any) => {
                //console.error('')
            });
        }
    }

    // 
    toggleCustomRowAd(isShow: boolean = true) {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log(`:${isShow}!`)
            return
        }
        if (this.customRowAd) isShow ? this.customRowAd.show() : this.customRowAd.hide();
    }

    // 
    initCustomColAd() {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log('!')
            return
        }
        if (this.customColId == '') {
            //console.log('ID')
            return
        }
        let winSize = platform.getSystemInfoSync();
        if (this.customColAd == null) {
            this.customColAd = platform.createCustomAd({
                adUnitId: this.customColId,
                adIntervals: 30,
                style: {
                    width: 80,
                    left: winSize.screenWidth - 60,
                    top: 20,
                    fixed: 0
                }
            })
            this.customColAd.onError((err: any) => {
                //console.error('')
            });
        }
    }

    // 
    toggleCustomColAd(isShow: boolean = true) {
        const platform = this.getPlatform()
        if (!platform) {
            //console.log(`:${isShow}!`)
            return
        }
        if (this.customColAd) isShow ? this.customColAd.show() : this.customColAd.hide();
    }

    // 
    getRank() {
        if (typeof window['wx'] === 'undefined') {
            //console.log('!')
            return
        }
        window['wx'].postMessage({
           // event: 'getRank'
        })
    }

    /**
     * 
     * @param data 
     */
    setRank(data: number) {
        if (typeof window['wx'] === 'undefined') {
            //console.log('!', data)
            return
        }
        window['wx'].postMessage({
            //event: 'setRank',
            data: data
        })
    }

    // 
    recordingVideoStart() {
        if (typeof window['tt'] === 'undefined') {
            //console.log('!')
            return
        }
        this.videoRecorder = window['tt'].getGameRecorderManager();
        this.videoRecordState = 1;
        this.videoIsExist = 0;
        this.videoStartTime = Date.parse(new Date().toString())
        // 
        this.videoRecorder.onStart((res: any) => {
            //console.log('');
            //console.log(res);
        });
        // 
        this.videoRecorder.start({
            duration: this.videoRecordTime, // 
        });
        // 
        this.videoRecorder.onStop((res: any) => {
            this.videoRecordState = 2;
            this.videoPath = res.videoPath;
            //console.log('', this.videoPath)
        });
        // 
        this.videoRecorder.onError((res: any) => {
            //console.log("error", res)
        });
    }

    // 
    recordingVideoEnd() {
        if (typeof window['tt'] === 'undefined') {
            //console.log('!')
            return
        }
        if (this.videoRecorder) {
            const endTime = Date.parse(new Date().toString())
            if ((endTime - this.videoStartTime) / 1000 <= 5) {
                this.videoIsExist = 1;
            } else {
                this.videoIsExist = 0;
            }
            this.videoRecorder.stop();
        }
    }

    /**
    * 
    * 1 
    * 2 
    */
    recordingVideoShare(success: any, fail?: any) {
        if (typeof window['tt'] === 'undefined') {
            //console.log('!')
            return
        }
        if (this.videoIsExist == 1) {
            //console.log('')
            return;
        }
        this.videoRecordState = 3;
        window['tt'].shareAppMessage({
            channel: 'video',
            title: '',
            imageUrl: '',
            query: '',
            extra: {
                videoPath: this.videoPath, // 
                videoTopics: this.videoTopics,
                createChallenge: true
            },
            success() {
                success && success()
            },
            fail(err: any) {
                fail && fail()
            }
        })
    }

    // 
    isVideoExist() {
        return this.videoIsExist
    }

    // codeopenid
    getLoginCode(callback?: (param: ICallBack) => void) {
        const obj: ICallBack = { status: false, data: null, msg: '' }
        window['wx'].login({
            success(res: any) {
                if (res.code) {
                    obj.status = true
                    obj.data = res.code
                    obj.msg = 'code'
                    callback && callback(obj)
                } else {
                    callback && callback(obj)
                }
            },
            fail() {
                callback && callback(obj)
            }
        })
    }

    // 
    getLoginInfo(callback?: (param: ICallBack) => void) {
        if (typeof window['wx'] === 'undefined') {
            //console.log('[Authorized login] Only supports WeChat platform!')
            return
        }
        const obj: ICallBack = { status: false, data: null, msg: 'Server exception' }
        window['wx'].getSetting({
            success(res: any) {
                if (res.authSetting["scope.userInfo"]) {
                    window['wx'].getUserInfo({
                        success(res: any) {
                            const info = res.userInfo
                            const user: IUser = { openid: uuid(), nickname: info.nickName, gender: info.gender, avatar: info.avatarUrl }
                            obj.status = true
                            obj.data = user
                            obj.msg = 'Authorized'
                            callback && callback(obj)
                        },
                        fail() {
                            callback && callback(obj)
                        }
                    })
                } else {
                    const sys = window['wx'].getSystemInfoSync()
                    const width = sys.screenWidth
                    const height = sys.screenHeight
                    let button = window['wx'].createUserInfoButton({
                        type: 'text',
                        text: '',
                        style: {
                            left: 0,
                            top: 0,
                            width: width,
                            height: height,
                            backgroundColor: '#00000000',
                            color: '#ffffff',
                            fontSize: 20,
                            textAlign: 'center',
                            lineHeight: height,
                        }
                    })
                    const tapEvent = function (res: any) {
                        button.destroy()
                        if (res.userInfo) {
                            const info = res.userInfo
                            const user: IUser = { openid: uuid(), nickname: info.nickName, gender: info.gender, avatar: info.avatarUrl }
                            obj.status = true
                            obj.data = user
                            obj.msg = 'Accept authorization'
                            callback && callback(obj)
                        } else {
                            obj.msg = 'Deny authorization'
                            callback && callback(obj)
                        }
                    }
                    button.onTap(tapEvent)
                }
            },
            fail() {
                callback && callback(obj)
            }
        })
    }

    // 
    getLoginInfoLocal(callback?: (param: ICallBack) => void) {
        const obj: ICallBack = { status: true, data: null, msg: 'Simulate user data' }
        // 4
        const time = new Date().getTime()
        let nickname = `${time}`
        nickname = 'NO.' + nickname.substring(nickname.length - 4)
        const user: IUser = { openid: uuid(), nickname: nickname, gender: 0, avatar: '' }
        obj.data = user
        callback && callback(obj)
    }
}

